import axios from 'axios';

import React,{Fragment, useEffect, useState} from  'react';
import { Form, FormGroup ,Button,Container,Input} from 'reactstrap';
import { toast } from 'react-toastify';
import base_url from '../api/bootapi';
const AddCourse = () => {

    useEffect(() => {
           document.title = "Add New Course" 
           loadFunction();
    },[])

    const [course,setCourse] = useState({});

    console.log(course);

    //form handler func
    const handleForm = (e) => {
        console.log(course);
        postDataToServer(course);
        e.preventDefault();
    }


    //post data to server
    const postDataToServer = (data) => {
        axios.post(`/courses`,data).then(
            (response) => {
                console.log("success");
                toast.success("course has been added",{});
            },
            (error) => {
                console.log("error");
                toast.error("something went wrong",{});
            }
        );
    }

    const loadFunction = () => {
        axios.get(`/courses`).then(
            (response) => {
                console.log("success");
                setCourse(response.data);
            },
            (error) => {
                console.log("error");
            }
        )
    }

    return(
        <Fragment>
            <h1 className="text-center my-2">Fill Course details</h1>
            <Form onSubmit={handleForm}>
                <FormGroup>
                    <label for="userId">Course Id</label>
                    <Input 
                        type="text"
                        placeholder="Enter user id"
                        name="userId"
                        id="userId"
                        onChange={(e) => {
                            setCourse({...course,id:e.target.value});
                        }}
                        value={course.id}
                    />
                </FormGroup>

                <FormGroup>
                    <label for="title">Course Title</label>
                    <Input
                        type="text"
                        placeholder="Enter title"
                        name="title"
                        id="title"
                        onChange={(e) => {
                            setCourse({...course,title:e.target.value});
                        }}
                        value={course.title}
                    />
                </FormGroup>

                <FormGroup>
                    <label for="description">Course Description</label>
                    <Input
                        type="textarea"
                        placeholder="Enter description"
                        name="description"
                        id="description"
                        style={{height:'150px'}}
                        onChange={(e) => {
                            setCourse({...course,description:e.target.value});
                        }}
                        value={course.description}
                    />
                </FormGroup>
                <div className="row">
                    <div className="col-md-6">
                    <FormGroup>
                                    <label for="phone">Contact No.</label>
                                    <Input
                                        type="text"
                                        placeholder="Enter Phone"
                                        name="phone"
                                        id="phone"
                                        onChange={(e) => {
                                            setCourse({...course,phone:e.target.value});
                                        }}
                                        value={course.phone}
                                    />
                                </FormGroup>

                    </div>
                    <div className="col-md-6">
                    <FormGroup>
                                    <label for="pincode">Pincode</label>
                                    <Input
                                        type="text"
                                        placeholder="Enter Pincode"
                                        name="pincode"
                                        id="pincode"
                                        onChange={(e) => {
                                            setCourse({...course,pincode:e.target.value});
                                        }}
                                        value={course.pincode}
                                    />
                                </FormGroup>

                    </div>
                </div>

                <Container className="text-center">
                    <Button type="submit" color="success">Add Course</Button>
                    {/*
                        <Button className="ml-2" onClick={() => {
                        console.log(course.id);
                        }}>Read</Button>
                    */}
                    <Button type="reset" onClick={() => {
                        setCourse({id: "", title:"", description:""});
                    }} color="warning ml-2">Clear</Button>
                </Container>

            </Form>
        </Fragment>
    )
}

export default AddCourse;