import axios from 'axios';
import React,{useState,useEffect} from 'react';
import {Link, useParams} from 'react-router-dom';
import {toast} from 'react-toastify';
import 'react-confirm-alert/src/react-confirm-alert.css';
import { confirmAlert } from 'react-confirm-alert'; // Import
import { Button, Modal, ModalHeader, ModalBody, ModalFooter } from 'reactstrap';
import {Card,CardSubtitle,CardText ,Input,Container, CardBody} from 'reactstrap';
import base_url from '../api/bootapi';
import EditCo from './EditCo';
const Course = ({ course, update },props) => {

    const deleteCourse = (id) => {
        confirmAlert({
            title: 'Delete Course',
            message: 'Are you sure to delete this course ?',
            buttons: [
              {
                label: 'Yes',
                onClick: () => {
                    axios.delete(`/courses/${id}`).then(
                        (response) => {
                            toast.success("course deleted successfully",{});
                            update(id);
                        },
                        (error) => {
                            toast.error("Course could not deleted",{});
                        }
                    )
                }
              },
              {
                label: 'No',
                onClick: () => {
                } 
              }
            ]
          });
        
    }

    const {
        buttonLabel,
        className
      } = props;

      const [modal, setModal] = useState(false);
      const [unmountOnClose, setUnmountOnClose] = useState(true);

      const toggle = () => setModal(!modal);

    const updateCourse = (id) => {

        axios.get(`/courses/${id}`).then(
            (response) => {
                toast.success("updated");
                console.log(response.data);
            },
            (error) => {
                toast.error("error");
            }
        )
    }
    return(
        <Card className="text-center" style={{border:'1px solid black',marginBottom:10,padding:15,fontFamily:'verdana'}}>
            <CardBody>
                <CardSubtitle className="font-weight-bold" style={{fontSize:'20px'}}>{course.title}</CardSubtitle>
                <CardText>{course.description}</CardText>
                <Container className="text-center"> 
                {/* <Link className="btn btn-primary mx-2" to={`edit/${course.id}`}>Edit</Link> */}
                <Button color="primary" className="mr-2" onClick={toggle}>Edit</Button>
                <Modal className="custom-modal-style" isOpen={modal} toggle={toggle} className={className} unmountOnClose={unmountOnClose}>
                    <ModalHeader toggle={toggle}>Update Course Details</ModalHeader>
                    <ModalBody>
                        <EditCo id1={course.id}/>
                    </ModalBody>
                </Modal>
                    <Button color="danger" onClick={() => {
                        deleteCourse(course.id);
                    }}>Delete</Button>
                </Container>
            </CardBody>
        </Card>
    );
}

export default Course;